.slider__text {
  text-transform: uppercase;

  @include osvald(400);
  @include fonts(16px, 20px);
  @include offset(0, 0);
}

.slider__img {
  @media (min-width: $tablet-width) {
    width: auto;
    height: 450px;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    height: 500px;
  }
}

.slider__img-before {
  @media (min-width: $tablet-width) {
    width: auto;
    height: 514px;
  }
}

.slider__img--closed {
  display: none;
}

.slider__list {
  @include list-reset();

  margin-bottom: 28px;

  @media (min-width: $tablet-width) {
    margin-bottom: 12px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 33px;
  }
}

.slider__item {
  text-align: center;
}
/*Фильтр*/

.slider__box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 34px;

  @media (min-width: $tablet-width) {
    margin-bottom: 68px;
  }

  @media (min-width: $desktop-width) {
    width: 620px;
    padding-left: 52px;
    margin-bottom: 72px;
    justify-content: center;
  }
}

.slider__button {
  border: none;
  border-bottom: 1px dotted rgba($basic-black, 0.3);
  background-color: $dark-grey;

  &:first-of-type {
    margin-right: 17px;
  }

  @media (min-width: $tablet-width) {
    @include fonts(20px, 30px);
    /*&:first-of-type {
      padding-left: 60px;
    }*/
  }

  @media (min-width: $desktop-width) {
    &:first-of-type {
      margin-right: 0;
    }
  }

  &:hover {
    border-bottom: 1px solid rgba($basic-black, 0.4);
  }

  &:active {
    border: none;
    outline: none;
  }
}

.slider__check {
  width: 84px;
  height: 14px;
  background-color: $basic-white;
  border: 1px solid $special-light-grey;
  border-radius: 6px;
  box-sizing: border-box;
  margin: 2px 18px 0 0;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.slider__check-bar {
  width: 35px;
  height: 6px;
  background-color: $basic-green;
  border-radius: 6px;
  margin: 3px 6px;
  box-sizing: border-box;
}

.slider__scale {
  @media (min-width: $tablet-width) {
    position: relative;
    padding: 12px 11px 0 16px;
  }

  @media (min-width: $desktop-width) {
    position: relative;
    padding: 12px 24px 0 19px;
  }
}

.slider__scale-bar {
  @media (min-width: $tablet-width) {
    width: 428px;
    height: 6px;
    background-color: $medium-grey;
    border-radius: 6px;
  }
}

.slider__scale-control {
  @media (min-width: $tablet-width) {
    background-color: $basic-white;
    border: 1px solid $special-light-grey;
    border-radius: 50%;
    width: 34px;
    height: 34px;
    box-sizing: border-box;
    position: absolute;
    top: -2px;
    left: 205px;
    z-index: 2;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: block;
      width: 8px;
      height: 8px;
      background-color: $basic-green;
      border-radius: 50%;
    }

    @media (min-width: $desktop-width) {
      top: 0;
      left: 222px;
    }

    &:hover {
      box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
    }

    &:active {
      border: solid 1px $basic-green;
      box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
    }
  }
}
