@mixin fonts($size, $line) {
  font-size: $size;
  line-height: $line;
}

@mixin osvald($weight) {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: $weight;
}

@mixin offset($padding, $margin) {
  padding: $padding;
  margin: $margin;
}

@mixin margin($right, $left) {
  margin-right: $right;
  margin-left: $left;
}

@mixin padding($right, $left) {
  padding-right: $right;
  padding-left: $left;
}

@mixin list-reset() {
  list-style: none;
  padding: 0;
  margin: 0;
}

@mixin link() {
  text-decoration: none;
  padding: 0;
  margin: 0;
}

@mixin color($color, $opacity) {
  color: $color;
  opacity: $opacity;
}

@mixin background($color, $opacity) {
  background-color: $color;
  opacity: $opacity;
}

@mixin size($width, $height) {
  width: $width;
  min-height: $height;
}
